import React from 'react'
import clsx from 'clsx'
import ReactTooltip from 'react-tooltip'
import { FiHelpCircle } from 'react-icons/fi'

const CalculatorRow = ({
  label,
  value,
  name,
  readOnly,
  onChange,
  tip,
  variant,
  type = 'number'
}) => {
  return (
    <tr>
      <td
        className={clsx(
          readOnly ? 'bg-background' : 'bg-muted font-semibold',
          variant === 'long' && 'text-green font-bold',
          variant === 'short' && 'text-red font-bold',
          !variant && 'text-text font-semi-bold',
          'truncate p-1 w-2/3 border border-highlight'
        )}>
        <div className='flex items-center'>
          <div className='mr-2 ml-1'>
            {tip && (
              <div>
                <div data-tip data-for={label} className='flex items-center'>
                  <FiHelpCircle className='text-gray-300 transition-all hover:text-lgray inline-block h-5 w-5 cursor-pointer' />
                </div>
                <ReactTooltip
                  id={label}
                  event='click focus'
                  className='customTooltipTheme'
                  globalEventOff='click'
                  effect='solid'
                  place={'right'}
                  clickable>
                  <div className='text-xs font-light p-1'>{tip}</div>
                </ReactTooltip>
              </div>
            )}
          </div>

          <div className='text-sm'>{label}</div>
        </div>
      </td>
      <td className='p-1 w-1/3 border border-highlight'>
        <input
          className={clsx(
            'text-sm w-full text-right bg-background',
            variant === 'long' && 'text-green font-bold',
            variant === 'short' && 'text-red font-bold',
            type === 'text' && 'text-red font-bold',
            !variant && (readOnly ? 'text-lgray ' : 'text-text')
          )}
          type={type}
          name={name}
          onChange={onChange}
          value={value}
          readOnly={readOnly}
          {...(readOnly && { tabIndex: '-1' })}
        />
      </td>
    </tr>
  )
}

export default CalculatorRow
